import { Component, OnInit } from '@angular/core';
import { ServerService } from 'src/app/services/server.service';
import { AuthService } from 'src/app/services/auth.service';
import { Router, ActivatedRoute } from '@angular/router';
import { ASSETS } from 'src/app/services/assets';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  me:any;
  addresses:any;
  showAddAddress = true;
  address = ASSETS+ '/addaddress.svg';
  loading = ASSETS+ '/loading.svg';
  noadd= false;

  constructor(private ss:ServerService,private auth:AuthService,private route:Router, private activeroute:ActivatedRoute) {
   }

  ngOnInit() {
    this.ss.getme(this.auth.getAccessToken()).subscribe(
      res=> {
        // console.log(res);
        this.me = res;
      },err => {
        // console.log(err);
      }
    )

    this.ss.getAddress(this.auth.getAccessToken()).subscribe(
      res => {
        // console.log(res)
        this.addresses = res;
        if(this.addresses.length == 0){
          this.noadd = true;
        }
      }, err=> {
        // console.log(err)
      }
    )
  }

  onEdit(id:any){
    this.route.navigate(['./profile-address/'+id],{relativeTo:this.activeroute})
  }

  onDelete(id:any){
      this.ss.deletdAddress(this.auth.getAccessToken(),id).subscribe(
        res=>{
          // console.log(res);
          this.ngOnInit();
        },err => {
          // console.log(err);          
        }
      )
    
  }
  

  onadd(){
    this.route.navigate(['./profile-address/',''],{relativeTo:this.activeroute})
  }

  addAddress(){
    if(this.addresses){

    if(this.addresses.length == 2){
      return false
    }else {
      return true
    }
  }
}

onLogout(){
  this.auth.removeAccessToken();
  this.auth.removeRefreshToken();
  this.route.navigate(['/']);
}

  
}
