import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { MainComponent } from './main.component';
import { HomeComponent } from './home/home.component';
import { RegisterComponent } from './user-auth/register/register.component';
import { LoginComponent } from './user-auth/login/login.component';
import { MyOrdersComponent } from './my-orders/my-orders.component';
import { ActivateComponent } from './user-auth/activate/activate.component';
import { SubcategoriesComponent } from './subcategories/subcategories.component';
import { ProfileComponent } from './myprofile/profile/profile.component';
import { HireProfessionalComponent } from './hire-professional/hire-professional.component';
import { AboutUsComponent } from './about-us/about-us.component';
import { PrivacyPolicyComponent } from './privacy-policy/privacy-policy.component';
import { AboutOrderComponent } from './about-order/about-order.component';
import { QuestionnaireComponent } from './questionnaire/questionnaire.component';
import { QuestionsComponent } from './questionnaire/questions/questions.component';
import { AddressComponent } from './questionnaire/address/address.component';
import { AddAddressComponent } from './questionnaire/add-address/add-address.component';
import { MyprofileComponent } from './myprofile/myprofile.component';
import { ProfileaddressComponent } from './myprofile/profileaddress/profileaddress.component';
import { CommonModule } from '@angular/common';
import { AuthGuard } from '../services/authguard.service';
import { DevTeamComponent } from './dev-team/dev-team.component';
import { PagenotfoundComponent } from './pagenotfound/pagenotfound.component';
import { ContactusComponent } from './home/contactus/contactus.component';

const routes: Routes = [

  {

    path: '', component: MainComponent, children: [
      { path: '', component: HomeComponent },
      { path: 'register', component: RegisterComponent,data: {title: 'Wofo24:: Sign Up'} },
      { path: 'login', component: LoginComponent,data: {title: 'Wofo24:: Login'} },
      { path: 'bookings',canActivate: [AuthGuard], component: MyOrdersComponent,data: {title: 'Wofo24:: My Bookings'} },
      { path: 'activate/:user_id', component: ActivateComponent,data: {title: 'Wofo24:: Activate'} },
      { path: 'subcategory/:param/:id', component: SubcategoriesComponent,data: {title: 'Wofo24:: Services'} },
      { path: 'hire-professional',canActivate: [AuthGuard], component: HireProfessionalComponent,data: {title: 'Wofo24:: Get Experts'} },
      { path: 'about-us', component: AboutUsComponent,data: {title: 'Wofo24:: About us'} },
      { path: 'privacy-policy', component: PrivacyPolicyComponent,data: {title: 'Wofo24:: Privacy Policy'} },
      { path: 'development-team', component: DevTeamComponent,data: {title: 'Wofo24:: Development Team'} },
      { path: 'order-detail/:id',canActivate: [AuthGuard], component: AboutOrderComponent,data: {title: 'Wofo24:: About Booking'} },
      {
        path: 'questions/:param/:id', component: QuestionnaireComponent, children: [
          { path: '', component: QuestionsComponent,data: {title: 'Wofo24:: Questionnaire'} },
          { path: 'address',canActivate: [AuthGuard], component: AddressComponent, pathMatch: 'full',data: {title: 'Wofo24:: Address'} },
          { path: 'add-address',canActivate: [AuthGuard], component: AddAddressComponent, pathMatch: 'full',data: {title: 'Wofo24:: Add Address'} },
          { path: 'add-address/:id',canActivate: [AuthGuard], component: AddAddressComponent, pathMatch: 'full',data: {title: 'Wofo24:: Edit Address'} },
        ]
      },
      {
        path: 'profile',canActivate: [AuthGuard], component: MyprofileComponent, children: [
          { path: '',canActivate: [AuthGuard], component: ProfileComponent,data: {title: 'Wofo24:: Profile'} },
          { path: 'profile-address',canActivate: [AuthGuard], component: ProfileaddressComponent,data: {title: 'Wofo24:: Profile'} },
          { path: 'profile-address/:id',canActivate: [AuthGuard], component: ProfileaddressComponent,data: {title: 'Wofo24:: Profile'} }

        ]
      },
      {
        path:'**',
        component:PagenotfoundComponent
      }    
    ],
    
  }
  

];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [RouterModule]

})
export class MainRoutingModule { }
