import { Component, OnInit } from '@angular/core';
import { AuthService } from 'src/app/services/auth.service';
import { ASSETS } from 'src/app/services/assets';
import { Router } from '@angular/router';
import Swal from 'sweetalert2';


@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {

  constructor(private auth:AuthService, private route:Router) { }
  logo = ASSETS+ '/logo.png';
  hire = ASSETS+ '/experts.svg';


  ngOnInit() {
  }

  check() {
    return this.auth.isLoggedIn();
  }

  onLogout(){
    this.auth.removeAccessToken();
    this.auth.removeRefreshToken();
    this.route.navigate(['/']);
  }

  onBecome(){
    Swal.fire({
      title: 'Become a professional ',
      html:' <span style="color:#F7BE52;">Drop a mail at: &nbsp;</span><span style="color:#1D4793;"> info@wofo24.com</span> <br>'+
      '<span style="color:#F7BE52;">or call us at: </span>&nbsp; <span style="color:#1D4793;"> +91 7524066047 </span>',
      
      imageUrl: this.hire,
      imageWidth: 400,
      imageHeight: 200,
      imageAlt: 'Custom image',
      confirmButtonColor: '#F7BE52',
    })
  }

  close(){
    document.getElementById('closebtn').click();
  }

  

}
