export class PlaceOrder {
    public response:string =null;
    public service_status: string ='';
    public payment_status: boolean = false;
    public address: number = null;
    public subcategory: number = null;
    public professional_assigned: number = null;
    public service_time: string = null;

    constructor(response:string,service_status:string,payment_status:boolean,address:number,subcategory:number,professional_assigned:number,service_time:string ) {
        this.response = response;
        this.service_status = service_status;
        this.payment_status = payment_status;
        this.address = address;
        this.subcategory = subcategory;
        this.professional_assigned = professional_assigned;
        this.service_time = service_time;
     }
}
