import { Component, OnInit } from '@angular/core';
import { ServerService } from 'src/app/services/server.service';
import { AuthService } from 'src/app/services/auth.service';
import { ASSETS } from 'src/app/services/assets';


@Component({
  selector: 'app-hire-professional',
  templateUrl: './hire-professional.component.html',
  styleUrls: ['./hire-professional.component.css']
})
export class HireProfessionalComponent implements OnInit {
  categories: any;
  categoryId: any;
  professionals: any;
  showProfessionals: any[] = [];
  professionalId: any;
  booked_professionals: any[] = [];
  noProfessional:boolean = false;
  plumberwall = ASSETS+ '/plumberwall.png';
  hire_prof = ASSETS+ '/hire-prof.svg';
  loading = ASSETS+ '/loading.svg';
  Hire = new Map();

  constructor(private ss: ServerService, private auth: AuthService) { }

  ngOnInit() {
    this.booked_professionals = []
    this.ss.getCategories().subscribe(
      res => {
        this.categories = res;
      },
      err => {
        // console.log(err);
      }
    )


    this.ss.getprofessionals().subscribe(
      res => {
        // console.log(res);
        this.professionals = res;
        if(this.professionals.length == 0){
          this.noProfessional = true;
        }
        for (var i = 0; i < this.professionals.length; i++) {
          this.showProfessionals.push(this.professionals[i]);
          this.Hire.set(this.professionals[i].id,true)
        }

      }, err => {
        // console.log(err);
      }
    )
  }

  onCategory(id: any) {
    this.showProfessionals = [];
    if (id != 0) {
      this.categoryId = id;
      for (var i = 0; i < this.professionals.length; i++) {
        for (var j = 0; j < this.professionals[i].subcategory.length; j++) {
          if (this.professionals[i].subcategory[j].category.id == this.categoryId) {
            this.showProfessionals.push(this.professionals[i]);
            break;
          }
        }
      }
    } else {
      for (var i = 0; i < this.professionals.length; i++) {
        for (var j = 0; j < this.professionals[i].subcategory.length; j++) {
          this.showProfessionals.push(this.professionals[i]);
          break;
        }
      }
    }
    // console.log(this.showProfessionals)
    if(this.showProfessionals.length == 0){
      this.noProfessional = true;
    }else{
      this.noProfessional = false
    }

  }


  onBook(id: any) {
    this.professionalId = id;
    this.booked_professionals.push(id)
    // console.log(this.professionalId);
    this.ss.getHireProfessional(this.auth.getAccessToken(), this.professionalId).subscribe(
      res => {
        // console.log(res)
        this.Hire.set(id,false);
        
      }, err => {
        // console.log(err)
      }
    )
  }



}
