import { Injectable } from '@angular/core';
import * as jwt_decode from "jwt-decode";
import { ServerService } from './server.service';
import { promise } from 'protractor';
import { Router } from '@angular/router';
// import { DatePipe } from '@angular/common';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  currentDate:any;
  newAccessToken:any;
  constructor(private ss:ServerService, private route:Router) { }

  storeRefreshToken(token: string) {
    localStorage.setItem('Refresh', token);
  }

  storeAccessToken(token: string) {
    localStorage.setItem('Access', token);
  }

  getRefreshToken() {
    return localStorage.getItem('Refresh')
  }

  getAccessToken() {
    return localStorage.getItem('Access')
  }

  isLoggedIn() {
    this.currentDate = new Date();
    if (this.getAccessToken() ) {
      if(this.currentDate > this.getRefreshTokenExpirationDate(this.getRefreshToken())){
        this.removeAccessToken();
        this.removeRefreshToken();
        this.route.navigate(['/login'])
        return false;
    }
      else if(this.currentDate < this.getTokenExpirationDate(this.getAccessToken())){
        return true
      } else {
        this.removeAccessToken();
        const refresh={
          "refresh" : this.getRefreshToken()
        }

        this.ss.postRefreshToken(refresh).subscribe(
          res=>{
            this.newAccessToken = res;
            this.storeAccessToken(this.newAccessToken.access);
            return true;
          },err =>{
            // console.log(err);
            return false;
          }
        )
      }
    } else {
      return false
    }
  }


  getTokenExpirationDate(token: string): Date {
    const decoded = jwt_decode(token);

    if (decoded.exp === undefined) return null;

    const date = new Date(0);
    date.setUTCSeconds(decoded.exp);
    // console.log(date);
    return date;
  }

  getRefreshTokenExpirationDate(token: string): Date {
    const decoded = jwt_decode(token);

    if (decoded.exp === undefined) return null;

    const date = new Date(0);
    date.setUTCSeconds(decoded.exp);
    // console.log(date);
    return date;
  }


  removeAccessToken() {
    localStorage.removeItem('Access');
  }

  removeRefreshToken() {
    localStorage.removeItem('Refresh');
  }
}
