import { Component, OnInit } from '@angular/core';
import { ServerService } from 'src/app/services/server.service';
import { AuthService } from 'src/app/services/auth.service';
import { Router } from '@angular/router';
import { ASSETS } from 'src/app/services/assets';


@Component({
  selector: 'app-my-orders',
  templateUrl: './my-orders.component.html',
  styleUrls: ['./my-orders.component.css']
})
export class MyOrdersComponent implements OnInit {
  myOrders:any;
  cart = ASSETS+ '/cart.svg';
  noorders = false;
  loading = ASSETS+ '/loading.svg';


  constructor(private ss:ServerService, private auth:AuthService, private route:Router) { }

  ngOnInit() {
    this.ss.getMyOrder(this.auth.getAccessToken()).subscribe(
      res => {
        // console.log(res);
        this.myOrders  = res;
        if(this.myOrders.length == 0){
          this.noorders = true;
        }
      }, err => {
        console.log(err)
      }
    )
  }

  onOrderDeatil(id:any){

    this.route.navigate(['order-detail',id])

  }

}
