import { Component, OnInit } from '@angular/core';
import { ServerService } from 'src/app/services/server.service';
import { Router } from '@angular/router';
import { ASSETS } from 'src/app/services/assets';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css']
})
export class SearchComponent implements OnInit {
  categories :any;
  SEARCHBG =  ASSETS + '/search-bg.png';
  
  constructor(private ss:ServerService,private route:Router) { }

  ngOnInit() {
    this.ss.getCategories().subscribe(
      res=>{
        this.categories = res;
      },
      err => {
        // console.log(err);
      }
    )
  }

  onCategory(value:any){
    // console.log(value);
    const split = value.split("-");
    // console.log(split);
    const catid = split[1];
    // console.log(catid);
    const catval = split[0];
    // console.log(catval);

    this.route.navigate(['subcategory',catval,catid])

  }

 

}
