import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { ServerService } from 'src/app/services/server.service';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/services/auth.service';
import { ASSETS } from 'src/app/services/assets';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  signupForm: FormGroup;
  user:any;
  alreadyExist:boolean =false;

  direction = ASSETS+ '/direction.svg';
  dots = ASSETS+ '/dots.svg';
  constructor(private ss:ServerService, private route:Router, private fb:FormBuilder, private auth:AuthService) {
    if(this.auth.isLoggedIn()){
      this.route.navigate(['/'])
    }
    

    let numberFormat = '[6-9][0-9]{9}';
    let alphabet = '^[a-zA-Z ]*$'
    this.signupForm = this.fb.group({
      'phone_number': new FormControl(null, Validators.compose([Validators.required,Validators.pattern(numberFormat)])),
      'name': new FormControl (null, Validators.compose([Validators.required,Validators.pattern(alphabet)]))
    })
   }

  ngOnInit() {
  }

  OnSignup(){
      // console.log(this.signupForm.value);
      this.ss.postSignup(this.signupForm.value).subscribe(
        res=>{
          // console.log(res);
          this.user = res;
          this.signupForm.reset();
          this.route.navigate(['activate',this.user.user_id])
        }, err => {
          // console.log(err);
          // console.log(err.error.error[0])
          if(err.error.error[0] == "phone number already exists"){
            this.alreadyExist = true;
          }
        }
      )
  }

  onLogin(){
    this.route.navigate(['login']);
  }

  removeval(){
    document.getElementById('val').innerHTML = '';
  }

  

}
