import { Component, OnInit } from '@angular/core';
import { Meta } from '@angular/platform-browser';
import { ActivatedRoute } from '@angular/router';
import { ServerService } from 'src/app/services/server.service';
import { MainService } from '../main.service';
import { ASSETS } from 'src/app/services/assets';
import Swal from 'sweetalert2';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';


@Component({
  selector: 'app-subcategories',
  templateUrl: './subcategories.component.html',
  styleUrls: ['./subcategories.component.css']
})
export class SubcategoriesComponent implements OnInit {
  subcategories: any;
  showSubcategories:any;
  subcat:boolean = false;
  verified = ASSETS+ '/verified@2x.png';
  damage = ASSETS+ '/damage@2x.png';
  price = ASSETS+ '/price@2x.png';
  doorstep = ASSETS+ '/doorstep@2x.png';
  mobile = ASSETS+ '/mobile.png';
  google = ASSETS+ '/google-play-badge.png';
  loading = ASSETS+ '/loading.svg';
  defaultImage = ASSETS+ '/electrician.png';
  getAppForm:FormGroup;
  
  constructor(private activeroute:ActivatedRoute,private ss:ServerService, private mainservice:MainService, private fb:FormBuilder, private meta: Meta) { 
    let numberFormat = '[6-9][0-9]{9}';

    this.getAppForm = this.fb.group({
      'phone_number': new FormControl(null, [ Validators.pattern(numberFormat)])
    });

      const viewport = this.meta.getTag('name=viewport');
      console.log(viewport.content); 

      this.meta.addTags([
        { name: 'twitter:card', content: 'summary_large_image' },
        { name: 'twitter:site', content: '@alligatorio' },
      ]);

  }

  ngOnInit() {
    this.activeroute.params.subscribe(params => {
      // console.log(params['id']);
      this.ss.getSubCategories(params['id']).subscribe(
        res=>{
          // console.log(res);
          this.showSubcategories = res;
          
          if(this.subcategories){
            this.subcat= true;
          }
          
        }, err=> {
          // console.log(err);
        }
      )
    })
  }

  ongetApp() {
    // console.log(this.getAppForm.value);
    if(!(this.getAppForm.value.phone_number == null || this.getAppForm.value.phone_number == "")){
      this.ss.postLink(this.getAppForm.value).subscribe(
        res => {
          // console.log(res);
          this.getAppForm.reset();
          Swal.fire({
            title: 'Link Sent',
            text: 'Link for WOFO24 app link is sent to your mobile number.',
            type: 'success',
            confirmButtonText: 'Ok',
          })
        }, err => {
          // console.log(err)
          Swal.fire({
            title: 'Try Again',
            text: 'Something went wrong, Please try again.',
            type: 'error',
            confirmButtonText: 'Ok',
          })
        }
      )
    }
   
  }

}
