import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ServerService } from 'src/app/services/server.service';
import { AuthService } from 'src/app/services/auth.service';
import { MainService } from '../../main.service';
// import { ToastrService } from 'ngx-toastr';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-activate',
  templateUrl: './activate.component.html',
  styleUrls: ['./activate.component.css']
})
export class ActivateComponent implements OnInit {
  otpForm: FormGroup;
  token: any;
  userId: any;
  Toast = Swal.mixin({
    toast: true,
    position: 'top-end',
    showConfirmButton: false,
    timer: 3000
  })
  constructor(private activeroute: ActivatedRoute, private ss: ServerService, private auth: AuthService, private route: Router, private mainservice: MainService
    ) {
      if(this.auth.isLoggedIn()){
        this.route.navigate(['/'])
      }

      
     }

  ngOnInit() {


    this.otpForm = new FormGroup({
      'otp': new FormControl(null, [Validators.required,Validators.minLength(4),Validators.maxLength(4)])
    })
    this.activeroute.params.subscribe(params => {
      // console.log(params['user_id']);
      this.userId = params['user_id'];
      // console.log(this.userId)
    }
      )

  }

  onOtp() {
    
      this.ss.postOtp(this.userId, this.otpForm.value).subscribe(
        res => {
          // console.log(res);
          this.token = res;
          this.auth.storeRefreshToken(this.token.refresh);
          this.auth.storeAccessToken(this.token.access);

          this.otpForm.reset();
          if (this.mainservice.response) {
            console.log(this.mainservice.subcategory)
            this.route.navigate(['questions',this.mainservice.subcategory, this.mainservice.subcategoryID, 'address'])
          } else {
            this.route.navigate(['']);
          }
        }, err => {
          // console.log(err);
          this.otpForm.reset();
        if(err.error.error == "Invalid OTP"){
          Swal.fire({
            title: 'Invalid OTP!',
            text: 'Please try again.',
            type: 'error',
            confirmButtonText: 'Ok',
          })
        } if(err.error.error == "OTP expired!" ) {
          Swal.fire({
            title: 'OTP Expired!',
            text: 'Please try again.',
            type: 'error',
            confirmButtonText: 'Ok',
          })
        }
        }
      )
    
  }


  onResend() {


    // console.log(this.userId)
    this.ss.getResend(this.userId).subscribe(
      res => {
        // console.log(res);
        // this.toastr.success('otp sent','otp succefully sent',{ positionClass: 'toast-bottom-right' });
        document.getElementById('sent').style.display = 'block'
        this.Toast.fire({
          type: 'success',
          title: 'OTP sent successfully'
        })
      }, err => {
        // console.log(err)
        
      }
    )
  }

  handleEvent(data: any) {
    if (data.action == "done") {
      document.getElementById('countdown').style.display = 'none';
      document.getElementById('resend').style.display = 'block';
    }
  }

  

}
