import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { ServerService } from 'src/app/services/server.service';
import { AuthService } from 'src/app/services/auth.service';
import { Router } from '@angular/router';
import { MainService } from '../../main.service';
import { ASSETS } from 'src/app/services/assets';
import Swal from 'sweetalert2';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  loginForm:FormGroup;
  user:any;
  doesnotexist= false;

  direction = ASSETS+ '/direction.svg';
  dots = ASSETS+ '/dots.svg';



  
  
  
  constructor(private ss:ServerService, private route:Router
  , private fb:FormBuilder, private auth:AuthService) { 
    
    if(this.auth.isLoggedIn()){
      this.route.navigate(['/'])
    }

    let numberFormat = '[6-9][0-9]{9}';
  
    this.loginForm = this.fb.group({
      'phone_number': new FormControl(null, Validators.compose([Validators.required,Validators.pattern(numberFormat)]))
    })
  }

  ngOnInit() {
    
  }

  onLogin(){
    this.ss.postLogin(this.loginForm.value).subscribe(
      res=>{
        // console.log(res);
        this.user = res;
        this.route.navigate(['activate',this.user.user_id])
      },
      err=>{
        // console.log(err);
        if(err.error.error == "Phone number doesn't exist"){
            this.doesnotexist = true;
        }
      }
    )

  }

  create(){
    this.route.navigate(['register'])
  }

}
