import { Component, OnInit } from '@angular/core';
import { ServerService } from 'src/app/services/server.service';
import { AuthService } from 'src/app/services/auth.service';
import { Router, ActivatedRoute } from '@angular/router';
import { PlaceOrder } from 'src/app/models/place-order';
import { MainService } from '../../main.service';
import { ASSETS } from 'src/app/services/assets';


@Component({
  selector: 'app-address',
  templateUrl: './address.component.html',
  styleUrls: ['./address.component.css']
})
export class AddressComponent implements OnInit {
  public placeOrder :PlaceOrder;
  Loading:boolean = false;
  addresses:any;
  addressId:any;
  disable = true;
  order:any;
  orderResponse:any;
  service_time:any;
  address = ASSETS+ '/addaddress.svg';
  loading = ASSETS+ '/loading.svg';

  date: Date = new Date();
    settings = {
        bigBanner: true,
        timePicker: true,
        format: 'dd-M-yyyy',
        defaultOpen: false,
        closeOnSelect:true
    }


  constructor(private ss:ServerService,private auth:AuthService, private route:Router, private activeRoute:ActivatedRoute, private mainservice:MainService) {
    this.placeOrder = new PlaceOrder(this.mainservice.response,'Pending',false,this.addressId,this.mainservice.subcategoryID,null,this.service_time);
   }

  ngOnInit() {
    if(this.mainservice.subcategoryID == null){
      this.route.navigate(['../'],{relativeTo:this.activeRoute})
    }

    this.ss.getAddress(this.auth.getAccessToken()).subscribe(
      res => {
        // console.log(res);
        this.addresses = res;
      },err=>{
        // console.log(err)
      }
    )
  }
  
  onOrder(){
    this.Loading = true;
    // console.log(this.addressId);
    // console.log(this.service_time);
    this.placeOrder.address = this.addressId;
    this.placeOrder.service_time = this.service_time;
    // console.log(this.placeOrder);
    // this.order = JSON.stringify(this.placeOrder);
    // console.log(this.order);
    this.ss.postOrder(this.auth.getAccessToken(), this.placeOrder).subscribe(
      res=>{
        // console.log(res);
        this.orderResponse = res;
        this.Loading = false;
        this.route.navigate(['order-detail',this.orderResponse.id])
      }, err =>{
        // console.log(err)
      }
    )
  }

  onselectedCheck(value:any){
    // console.log(value);
    this.addressId = value;
    if(this.addressId){
      this.disable = false;
    }
  }

  onadd(){
    this.route.navigate(['../add-address/'+''],{relativeTo:this.activeRoute})
  }

  onEdit(id:any){
    this.route.navigate(['../add-address/'+id],{relativeTo:this.activeRoute})
  }

  onDateSelect(date :any){
    date=date.toString()
    var month = date.slice(4,7)
    var day = date.slice(8,10)
    var year = date.slice(11,15)
    var time = date.slice(16,21)
    // console.log(day+ " " + month + " "+year+" | " + time)
    this.service_time = day+ " " + month + " "+year+" | " + time;
    // console.log(this.service_time)
  }

}
