import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { ServerService } from 'src/app/services/server.service';
import { AuthService } from 'src/app/services/auth.service';

@Component({
  selector: 'app-profileaddress',
  templateUrl: './profileaddress.component.html',
  styleUrls: ['./profileaddress.component.css']
})
export class ProfileaddressComponent implements OnInit {
  addressId:any;
  addAddressForm:FormGroup;
  addresses:any;
  pincodeFormat = '[1-9][0-9]{5}'

  constructor(private activeRoute:ActivatedRoute, private ss:ServerService, private auth:AuthService, private route:Router, private formbuilder:FormBuilder) {
    
   }

  ngOnInit() {
    this.addAddressForm = this.formbuilder.group({
      'house_no': new FormControl(null,[]),
      'street':  new FormControl(null, [Validators.required]),
      'city':  new FormControl(null, [Validators.required]),
      'state':  new FormControl(null, [Validators.required]),
      'postal_code':  new FormControl(null, [Validators.pattern(this.pincodeFormat)])

    })

    this.activeRoute.params.subscribe(params=> {
      // console.log(params['id']);
      this.addressId = params['id'];
      
    })

   


    this.ss.getAddress(this.auth.getAccessToken()).subscribe(
      res => {
        // console.log(res);
        this.addresses = res;
        for(var i=0;i< this.addresses.length;i++){
          if(this.addresses[i].id == this.addressId){
            this.addAddressForm.setValue({
              house_no: this.addresses[i].house_no,
              street: this.addresses[i].street,
              city: this.addresses[i].city,
              state: this.addresses[i].state,
              postal_code: this.addresses[i].postal_code,
            })
          }
        }
        if((this.addresses.length == 2) && (this.addressId == '')){
          this.route.navigate(['../address'],{relativeTo: this.activeRoute})
        }
        
      },err=>{
        // console.log(err);
      }
    )

   
    
  }

  onSave(){
    // console.log(this.addAddressForm.value);
    if(this.addressId == ''){
      // console.log(this.addressId);

      this.ss.postAddress(this.auth.getAccessToken(),this.addAddressForm.value).subscribe(
        res=>{  
          // console.log(res);
          this.addAddressForm.reset();
          this.route.navigate(['../../'],{relativeTo: this.activeRoute})
        },err=>{
          // console.log(err);
        }
      )
    } else {
      // console.log(this.addressId);
      this.ss.updateAddress(this.auth.getAccessToken(),this.addAddressForm.value,this.addressId).subscribe(
        res=>{
          // console.log(res);
          this.addAddressForm.reset();
          this.route.navigate(['../../'],{relativeTo: this.activeRoute})
        },err=>{
          // console.log(err);
        }
      )
    }
    
  }
}
